<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <form>
          <!--Search Input-->
          <div>
            <div class="row">
              <div class="col-md-1">
                <fg-input>
                  <el-input
                    type="search"
                    class="mb-6"
                    clearable
                    prefix-icon="el-icon-search"
                    style="width: 200px"
                    placeholder="Search records"
                    v-model="searchQuery"
                    aria-controls="datatables"
                  ></el-input>
                </fg-input>
              </div>
            </div>
          </div>
          <!--Input Fields-->
          <div class="row">
            <label class="col-form-label col-md-1 text-right">BLOCK</label>
            <div class="col-md-2">
              <fg-input type="text" v-model="block"></fg-input>
            </div>
            <label class="col-form-label">SQFT</label>
            <div class="col-md-2">
              <fg-input type="text" v-model="sqft"></fg-input>
            </div>
            <label class="col-form-label">AVAILABILITY</label>
            <div class="col-md-2">
              <toggle-button
                class="switch"
                :value="false"
                :width="90"
                :color="{checked: '#7DCE94', unchecked: '#BE3D62'}"
                :labels="{checked: 'EMPTY', unchecked: 'OCCUPIED'}"
                v-model="availability"
                @click.native="radiostatus(availability)"
              />
            </div>
            <!--Price Slider-->
            <label class="col-form-label">PRICE</label>
            <div class="col-md-2">
              <vue-slider class="priceSlider" v-model="value" v-bind="options"></vue-slider>
            </div>
          </div>
          <div class="row">
            <div class="col-md-1">
              <n-button
                class="btn btn-primary btn-sm"
                type="info"
                @click.native="get(block, sqft, enabledRadio, value[0], value[1])"
              >FILTER NOW</n-button>
            </div>
          </div>
        </form>
      </card>
      <!--Listing Table-->
      <card>
        <div slot="header">
          <h5 class="title">LISTING</h5>
        </div>
        <div class="col-12 d-flex align-items-end flex-column">
          <el-select
            class="select-primary p-2"
            style="width: 200px"
            v-model="pagination.perPage"
            placeholder="Per page"
          >
            <el-option
              class="select-default"
              v-for="item in pagination.perPageOptions"
              :key="item"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </div>
        <el-table :data="queriedData" @row-click="onRowClick">
          <el-table-column min-width="50" type="index" align="center"></el-table-column>
          <el-table-column min-width="150" prop="ownerName" label="NAME" align="center"></el-table-column>
          <el-table-column min-width="140" prop="blockno" label="BLOCK" align="center"></el-table-column>
          <el-table-column min-width="100" prop="sqft" label="SQFT" align="center"></el-table-column>
          <el-table-column min-width="140" prop="contact" label="CONTACT" align="center"></el-table-column>
          <el-table-column min-width="170" prop="status" label="AVAILABILITY" align="center"></el-table-column>
          <el-table-column min-width="180" prop="rentalFees" label="RENTAL FEES" align="center"></el-table-column>
        </el-table>
        <div
          slot="footer"
          class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap"
        >
          <div class>
            <p class="card-category">Showing {{from + 1}} to {{to}} of {{total}} entries</p>
          </div>
          <n-pagination
            class="pagination-no-border"
            v-model="pagination.currentPage"
            :per-page="pagination.perPage"
            :total="total"
          ></n-pagination>
        </div>
      </card>
    </div>
  </div>
</template>
<script>
import { Table, TableColumn, Select, Option } from "element-ui";
import vueSlider from "vue-slider-component";
import { Pagination as NPagination } from "src/components";
import Fuse from "fuse.js";

export default {
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    vueSlider,
    NPagination,
    [Select.name]: Select,
    [Option.name]: Option
  },
  computed: {
    /***
     * Returns a page from the searched data or the whole data. Search is performed in the watch section below
     */
    queriedData() {
      let result = this.listing;
      if (this.searchedData.length > 0) {
        result = this.searchedData;
      }
      return result.slice(this.from, this.to);
    },
    to() {
      let highBound = this.from + this.pagination.perPage;
      if (this.total < highBound) {
        highBound = this.total;
      }
      return highBound;
    },
    from() {
      return this.pagination.perPage * (this.pagination.currentPage - 1);
    },
    total() {
      return this.searchedData.length > 0
        ? this.searchedData.length
        : this.listing.length;
    }
  },
  data() {
    return {
      listing: [],
      value: [0, 250],
      options: {
        min: 0,
        max: 500,
        formatter: "RM{value}"
      },
      block: "",
      sqft: "",
      availability: false,
      enabledRadio: "OCCUPIED",
      pagination: {
        perPage: 5,
        currentPage: 1,
        perPageOptions: [5, 10, 25, 50],
        total: 0
      },
      searchQuery: "",
      propsToSearch: ["ownerName"],
      searchedData: [],
      fuseSearch: null
    };
  },
  methods: {
    //click row to locate to edit listing
    onRowClick(event) {
      window.location.href = `#/listing/editlisting/${event.id}`;
    },

    radiostatus(avai) {
      avai = !avai;
      if (avai == false) {
        this.enabledRadio = "OCCUPIED";
      } else {
        this.enabledRadio = "EMPTY";
      }
    },
    /*get records after pressing filter button, parameters passed are
    block, square_feet, enabledradio, price slider value1, price slider value2*/
    get(b, sf, er, value1, value2) {
      this.$http
        .get("https://admindashboard-b2ab9.firebaseio.com/listing.json")
        .then(function(data) {
          return data.json();
        })
        .then(function(data) {
          //console.log(value1)
          //console.log(value2)

          //filter by occupied
          if (er == "OCCUPIED") {
            var occupied = [];
            for (let key in data) {
              //if status = occupied and rental fees within range of slider
              if (
                data[key].status == er &&
                value1 <= data[key].rentalFees &&
                data[key].rentalFees <= value2
              ) {
                data[key].id = key;
                occupied.push(data[key]);
              }
            }
            this.listing = occupied;

            //filter by block only
            if (b) {
              var blocksearch_Array = [];
              for (let key in data) {
                //if status = occupied and block no = input and rental fees within range of slider
                if (
                  data[key].blockno == b &&
                  data[key].status == er &&
                  value1 <= data[key].rentalFees &&
                  data[key].rentalFees <= value2
                ) {
                  data[key].id = key;
                  blocksearch_Array.push(data[key]);
                }
              }
              this.listing = blocksearch_Array;
              //console.log(inactive_Array)
            }

            //filter by square feet only
            if (sf) {
              var sf_search_Array = [];
              for (let key in data) {
                //if status = occupied and sqft = input and rental fees within range of slider
                if (
                  data[key].sqft == sf &&
                  data[key].status == er &&
                  value1 <= data[key].rentalFees &&
                  data[key].rentalFees <= value2
                ) {
                  data[key].id = key;
                  sf_search_Array.push(data[key]);
                }
              }
              this.listing = sf_search_Array;
              //console.log(inactive_Array)
            }

            //filter by both block no and sqft
            if (b && sf) {
              var b_sf_search_Array = [];
              for (let key in data) {
                //if status = occupied and sqft = input  and block no = input and rental fees within range of slider
                if (
                  data[key].sqft == sf &&
                  data[key].blockno == b &&
                  data[key].status == er &&
                  value1 <= data[key].rentalFees &&
                  data[key].rentalFees <= value2
                ) {
                  data[key].id = key;
                  b_sf_search_Array.push(data[key]);
                }
              }
              this.listing = b_sf_search_Array;
              //console.log(inactive_Array)
            }
          }
          //filter by empty
          else {
            var empty = [];
            for (let key in data) {
              //if status = empty and rental fee within range of slider
              if (
                data[key].status == er &&
                value1 <= data[key].rentalFees &&
                data[key].rentalFees <= value2
              ) {
                data[key].id = key;
                empty.push(data[key]);
              }
            }
            this.listing = empty;

            //filter by block no only
            if (b) {
              var blocksearch_Array = [];
              for (let key in data) {
                //if status = empty and block no = input and rental fees within range of slider
                if (
                  data[key].blockno == b &&
                  data[key].status == er &&
                  value1 <= data[key].rentalFees &&
                  data[key].rentalFees <= value2
                ) {
                  data[key].id = key;
                  blocksearch_Array.push(data[key]);
                }
              }
              this.listing = blocksearch_Array;
              //console.log(inactive_Array)
            }

            //filter by square feet only
            if (sf) {
              var sf_search_Array = [];
              for (let key in data) {
                //if status = empty and sqft = input and rental fees within range of slider
                if (
                  data[key].sqft == sf &&
                  data[key].status == er &&
                  value1 <= data[key].rentalFees &&
                  data[key].rentalFees <= value2
                ) {
                  data[key].id = key;
                  sf_search_Array.push(data[key]);
                }
              }
              this.listing = sf_search_Array;
              //console.log(inactive_Array)
            }

            //filter by both block and square feet
            if (b && sf) {
              var b_sf_search_Array = [];
              for (let key in data) {
                //if status = empty and sqft = input and block no = input and rental fees within range of slider
                if (
                  data[key].sqft == sf &&
                  data[key].blockno == b &&
                  data[key].status == er &&
                  value1 <= data[key].rentalFees &&
                  data[key].rentalFees <= value2
                ) {
                  data[key].id = key;
                  b_sf_search_Array.push(data[key]);
                }
              }
              this.listing = b_sf_search_Array;
              //console.log(inactive_Array)
            }
          }
        });
    }
  },
  watch: {
    /**
     * Searches through the table data by a given query.
     * NOTE: If you have a lot of data, it's recommended to do the search on the Server Side and only display the results here.
     * @param value of the query
     */
    searchQuery(value) {
      let result = this.listing;
      if (value !== "") {
        result = this.fuseSearch.search(this.searchQuery);
      }
      this.searchedData = result;
    }
  },
  //get request when page is created
  created() {
    this.$http
      .get("https://admindashboard-b2ab9.firebaseio.com/listing.json")
      .then(function(data) {
        return data.json();
      })
      .then(function(data) {
        var list_Array = [];
        for (let key in data) {
          data[key].id = key;
          list_Array.push(data[key]);
        }
        this.listing = list_Array;

        this.fuseSearch = new Fuse(this.listing, {
          keys: ["ownerName"],
          threshold: 0.3
        });
      });
  }
};
</script>
<style scoped>
.display-inline {
  display: inline !important;
}
.switch,
.priceSlider {
  margin-top: 5px;
}
</style>
